@import "../../Assets/Variables.less";

@nav-border-val: 1px solid @color-black;

.Menu {
  position: sticky;
  top: 0;
  z-index: 20;
  background-color: @color-white;
  padding: 10px 5px 10px 0;
  margin: 10px @indent-lr 25px @indent-lr;
  min-width: 158px;
  border-top: @nav-border-val;
  border-bottom: @nav-border-val;
  display: flex;

  &__Item {
    display: flex;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
    line-height: 1.5em;
    color: @color-black;//white;
    margin-right: 10px;
    white-space: nowrap;

    > svg {
      margin-right: 5px;
    }

    @media (min-width: 700px) {
      margin-bottom: 10px;
    }

    &--active {
      text-decoration: none;
      &::after {
        margin-left: 5px;
        content: "✓";
        border-radius: 50%;
      }
    }
    &--unselect {
      user-select: none;
    }
    &--bg {
      margin-left: auto;
      margin-right: 0;
      border: none;
      font-size: 17px;
      outline: 0;
      padding: 0;
      font-family: inherit;
      background-color: transparent;
      user-select: none;
      &:focus-visible {
        outline: 2px solid #007acc; // default chrome
      }
      &:hover {
        color: @color-black;
      }
      // TODO: add breakpoint value, and recheck @mobile-width
      @media (max-width: @mobile-width-upd) {
        display: none;
      }
    }
  }

  &_List {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  
  &_List_Item {
    margin: 0;
    padding: 0;
  }
  
}

